import React from 'react'
import {Page} from './base.js'


function KitContent() {
    return (
    <div className="content">
      <div className="parts">
        <h2>DNA微量快速建库试剂盒</h2>
        <p>
        该产品是针对动物微量组织DNA提取纯化及建库的一体化试剂盒，需要起始样本少，仅需小米大小的组织块即可，不需要液氮研磨匀浆，减少了样品的损失提高了DNA提取和纯化的效率，提取的DNA可直接用于后续全基因组文库的构建，整个过程耗时短，半天可完成整个实验，操作简便，效率高，大大降低了成本。可用于各种动物组织的全基因组建库，例如肝，肺，脑，鼠尾，脾，肾等等。
        </p>
        <ol>
          <li>本产品可适用于小米粒大小的动物组织（如脑组织，肝组织，癌组织，脾组织，尾组织等）DNA提取建库。</li>
          <li>组织不需要研磨过程，可直接进行消化。</li>
          <li>本试剂盒从组织到文库纯化，仅需三个小时就能完成，耗时短，效率高。</li>
          <li>本试剂盒所建文库适用于Illumina测序平台。</li>
        </ol>
      </div>

      <div className="parts">
        <h2>转录组微量快速建库试剂盒</h2>
        <p>
        该试剂盒主要针对动物微量组织的RNA提取建库，起始样本组织少，无需要研磨，也不需要使用trizol，氯仿等RNA提取有毒试剂，大大提高了安全性。使用磁珠直接将裂解组织中的RNA吸附出来进行转录组链特异性和非特异性建库，更加的高效快捷，减少了RNA的损失，主要在微量组织，某个特定脑区等取材难度大的实验中应用广泛。
        </p>
        <ol>
          <li>本产品可适用于半米粒大小的动物组织（脑组织，肝组织，癌组织等）的转录组建库。</li>
          <li>取组织时尽量在无酶的环境下进行，组织未经过对核酸有影响的化学试剂等处理，取下的组织要尽量新鲜，及时使用或者放-80度暂放，否则影响建库效率。</li>
          <li>可选择链特异文库或者非链特异性文库。</li>
          <li>文库适用于illumina测序平台。</li>
        </ol>
      </div>

      <div className="parts">
        <h2>DLO-HiC建库试剂盒</h2>
        <p>
        该试剂盒是根据DLO-HiC的原理，设计出来的一款方便实用的建库试剂盒，在原来实验的基础上进行了优化改良，使实用性更强，操作更加的简便。需要的细胞模板量仅1×10<sup>5</sup>-5×10<sup>5</sup>个数量级，且避免了page胶的使用，降低了操作难度，改进了接头引物，减少了二聚体的产生，大大提高了扩增效率，降低了回收纯化的复杂性。整个实验过程仅需要两天即可得到合格的测序文库，且使用范围广泛，该试剂盒已经成功应用于各种细胞系，动物组织，肿瘤组织等等。
        </p>
        <ol>
          <li>本产品可应用于贴壁细胞，悬浮细胞，动物组织（研磨后细胞量特别小的除外），肿瘤组织（不能匀浆的除外）。</li>
          <li>从细胞组织样品到文库构建纯化，仅需要两天的时间，且操作简便，大大提高了效率。</li>
          <li>巧妙的引物设计，减少了文库扩增过程中引物二聚体的产生，可直接进行磁珠回收。避免低熔点胶回收纯化带来的繁琐，也提高了回收效率，降低了成本。</li>
          <li>得到文库后，可先进行酶切对文库质量进行初步评估，减少直接测序带来的损失。</li>
          <li>本试剂盒所建文库适用于Illumina测序平台。</li>
        </ol>
      </div>
    </div>
    )
}

export function Kit() {
  return (
    <Page
    header={
        <div className="header">
            <h1>快速建库试剂盒</h1>
            <p>用于动物组织的 DNA/RNA 的快速提取、纯化、建库</p>
        </div>
    }
    content={
      <KitContent />
    } />
  )
}
