import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom"
import React from 'react'
import Home from './Home.js'
import {Kit} from './Products.js'
import {Spatial} from './Research.js'


function App() {
  return (
    <Router>
      <Switch>
        <Route path="/products/kit">
          <Kit />
        </Route>
        <Route path="/research/spatial">
          <Spatial />
        </Route>
        <Route exact path="/">
          <Home />
        </Route>
      </Switch>
    </Router>
  )
}

export default App;
