import React from 'react'
import {Page} from './base.js'
import './css/Home.css'


// import images
import iss0 from "./img/iss-0.jpg";
import iss1 from "./img/iss-1.jpg";
import kit0 from "./img/dnakit-0.jpg";
import kit1 from "./img/dnakit-1.jpg";
import ish0 from "./img/ish-0.png";
import seq0 from "./img/seq-0.png";


function Header() {
  return (
    <div className="header-large">
      <h1>鲲羽 &nbsp;SpatialFish</h1>
      <p>助力科学探索，加快精准医疗，提倡创新转化，服务百姓健康</p>
    </div>
  )
}


function Show1() {
  return (
    <div className="show1">
      <div className="show1-left">
        <img src={iss0} alt="ISS"></img>
      </div>
      <div className="show1-right">
        <div>
          <h1>原位测序技术 - ISS</h1>
          <div>点亮，生命之光</div>
          <div><a href="/research/spatial">了解更多</a></div>
        </div>
      </div>
    </div>
  )
}


function Show2() {
  return (
    <div className="show2">
      <div className="show2-left">
        <div>
          <h1>快速建库试剂盒</h1>
          <div>NGS 实验中 DNA, RNA 的快速建库。</div>
          <div><a href="/products/kit">了解更多</a></div>
        </div>
      </div>
      <div className="show2-right">
        <img src={kit0} alt="ISS"></img>
      </div>
    </div>
  )
}



function InfoCards() {
  return (
    <div className="infoCards">
      <div className="card">
        <img className="titleImg" src={iss1} />
        <div className="cardContent">
          <div className="title">原位测序</div>
          <div className="desc">解析空间转录信息。</div>
          <a className="link" href="/research/spatial">阅读更多</a>
        </div>
      </div>
      <div className="card">
        <img className="titleImg" src={ish0} />
        <div className="cardContent">
          <div className="title">中低通量 FISH 检测</div>
          <div className="desc">临床与科研中的染色体结构变异检测。</div>
          <a className="link" href="#">阅读更多</a>
        </div>
      </div>
      <div className="card">
        <img className="titleImg" src={kit1} />
        <div className="cardContent">
          <div className="title">快速建库试剂盒</div>
          <div className="desc">用于动物组织的微量、快速建库。</div>
          <a className="link" href="/products/kit">阅读更多</a>
        </div>
      </div>
      <div className="card">
        <img className="titleImg" src={seq0} />
        <div className="cardContent">
          <div className="title">生物信息学服务</div>
          <div className="desc">提供配套生物信息学分析服务。</div>
          <a className="link" href="#">阅读更多</a>
        </div>
      </div>
    </div>
  )
}


export default function Home() {
  return (
    <Page
    header={
        <Header />
    }
    content={
      <div>
        <Show1 />
        <Show2 />
        <div className="board1">
          <h1>科研、产品、技术服务</h1>
          <p>专注铸就专业</p>
        </div>
        <InfoCards />
      </div>
    } />
  )
}
