import React from 'react'
import {Page} from './base.js'


function SpatialContent() {
    return (
    <div className="content">
      <div className="parts">
        <p>
        转录组测序技术实现了组织和细胞在转录水平的高通量解读，但往往缺乏空间维度上的信息展示。而空间转录组学结合了高通量和原位的特点，实现了转录组信息在空间维度上的高分辨率、单细胞和亚细胞水平的三维展示，将为生物、医学领域的研究与检测带来革命性的变化。该技术不仅能为生物、医学领域的研究如胚胎发育、疾病病理机制提供更加精确、完整的信息和线索，更能为医学病理诊断、预后评估提供更全面、更关键、更权威的数据。因此，空间转录组学在生物、医学领域有很好的市场前景和很强的发展潜力。
        </p>
        <p>
        机体的正常运转离不开生物分子在精密的空间结构上高度有序的完成各项使命，染色体的高级折叠、转录物和蛋白质在特定时间和空间的出现与降解决定了细胞在组织环境中的特殊身份。这对于理解疾病的发生发展和检测、诊断、治疗疾病均具有重要的指导意义。目前运用于市场的原位分子检测方法主要有FISH、经典原位杂交等，他们大多均存在通量低、稳定性差、操作繁琐等限制。本团队建立了基于单细胞测序、生物信息大数据分析、原位成像的方法全面还原生理病理状态下生物分子的精准分布。首先针对目的分子设计具有特定条形码的探针，对目标分子进行捕获，再运用连接测序技术对条形码进行解读，可实现高通量单细胞分辨率的成像分析。适用于肿瘤分子诊断、疾病早筛。针对以上技术我们设计了杂交、成像、分析一体化的高通量原位测序仪，该仪器具备通量高、反应条件恒定、多轮成像配比度高、检测分子可视化等优势。高通量原位测序仪的开发与应用将进一步推进精准医疗大时代的到来。
        </p>
      </div>

    </div>
    )
}

export function Spatial() {
  return (
    <Page
    header={
        <div className="header">
            <h1>空间转录组学</h1>
            <p>转录组信息在空间维度上的高分辨率、单细胞和亚细胞水平的三维展示</p>
        </div>
    }
    content={
      <SpatialContent />
    } />
  )
}
