import React from 'react'
import './css/w3.css'
import './css/base.css'

export function NavBar() {
  return (
    <div className="navbar">
      <a href="/">首页</a>
      <div className="dropdown">
        <div className="dropdown-title">产品介绍</div>
        <div className="dropdown-content">
          <a href="/products/kit">试剂盒</a>
        </div>
      </div>
      <div className="dropdown">
        <div className="dropdown-title">科研服务</div>
        <div className="dropdown-content">
          <a href="/research/spatial">原位测序</a>
        </div>
      </div>
      <div className="dropdown">
        <div className="dropdown-title">公司信息</div>
        <div className="dropdown-content">
          <a href="#">主要人员</a>
        </div>
      </div>
      <div className="dropdown">
        <div className="dropdown-title">战略合作</div>
        <div className="dropdown-content">
          <a href="#">人才招聘</a>
        </div>
      </div>
      <a href="#contact" className="right">联系我们</a>
    </div>
  )
}


export class Page extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="App">
        <NavBar />
        {this.props.header}
        <div className="pageContent">
          {this.props.content}
        </div>
        <Footer />
      </div>
    );
  }
}


function Footer() {
  return (
    <div className="footer">
      <a name="contact"></a>
      <div className="infoList">
        <div className="listItem" style={{"width": "35%"}}>
          <div className="itemTitle">公司简介</div>
          <div className="itemContent">
            <p>鲲宇生物科技是一家以科研、医疗领域技术服务为核心的创新型技术公司，
              承接中低通量FISH、原位测序、表观遗传学等领域的技术服务项目，
              如有需要请联系我们。</p>
          </div>
        </div>
        <div className="listItem" style={{"padding-left": "90px"}}>
          <div className="itemTitle" style={{"text-align": "left"}}>经营范围</div>
          <div className="itemContent">
            <p>中低通量FISH</p>
            <p>原位测序</p>
            <p>表观遗传学</p>
            <p>快速建库试剂盒</p>
          </div>
        </div>
        <div className="listItem"  style={{"width": "35%", "text-align": "left"}}>
          <div className="itemTitle">联系方式</div>
          <div className="itemContent">
            <p>联系电话：xxxxxxxxxx</p>
            <p>邮箱：xxxxxxxx@xxx.com</p>
            <p>公司地址：xxxxxxxxxxx</p>
            <p>邮编：xxxxxx</p>
          </div>
        </div>
      </div>
    </div>
  )
}
